import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';

import BasicLayout from "../../layouts/BasicLayout"
import axios from 'axios';

function LessonModify() {
  const [lessons, setLessons] = useState({});

  useEffect(() => {
    const fetchLessons = async () => {
      try {
        const response = await axios.get('/host/lesson/modify');
        setLessons(response.data);
      } catch (error) {
        console.error('에러 발생:', error);
      }
    };

    fetchLessons();
  }, []);

  return (
    <div>
      <BasicLayout>
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>

          <form>
            <h1>클래스 수정 페이지입니다</h1><br></br>
            <div>
              <label>
                <h4>클래스 이름</h4>
                {/* <input className='styled-input2' value={lessons.lessonName} onChange={(e) => handleInputChange(e, lessons.setLessonName)} placeholder='제목을 입력해 주세요.' required /> */}
                <input className='styled-input2' value={lessons.lessonName} placeholder='제목을 입력해 주세요.' required />
              </label>
              <hr />
            </div>

            <div>
              <label>
                <h4>카테고리</h4>
                {/* <select className='' value={lessons.lessonCategory} onChange={(e) => handleInputChange(e, lessons.setLessonCategory)} required> */}
                <select className='' value={lessons.lessonCategory} required>
                  <option value="" disabled>선택</option>
                  <option value='cook'>요리</option>
                  <option value='art'>미술</option>
                  <option value='music'>음악</option>
                  <option value='sport'>운동</option>
                  <option value='baking'>베이킹</option>
                </select><br />
              <hr />
              </label>
            </div>

            <div>
              <label>
                <h4>클래스 가격</h4>
                {/* <input className='styled-input2' value={lessons.price} type='number' onChange={(e) => handleInputChange(e, lessons.setPrice)} placeholder='숫자만 입력해 주세요.' required /><br /> */}
                <input className='styled-input2' value={lessons.price} type='number' placeholder='숫자만 입력해 주세요.' required /><br />
              </label>
              <hr />
            </div>

            <div>
              <label>
                {/* <h4>제한 인원</h4>
                <input className='styled-input2' value={lessons.lessonLimited} type='number' onChange={(e) => handleInputChange(e, lessons.setLessonLimited)} placeholder='숫자만 입력해 주세요.' required /><br /> */}
                <input className='styled-input2' value={lessons.lessonLimited} type='number' placeholder='숫자만 입력해 주세요.' required /><br />
              </label>
              <hr />
            </div>

            <div>
              <label>
                <h4>클래스 시작일</h4>
                {/* <input className='styled-date' value={lessons.lessonSchedule} type='date' onChange={(e) => handleInputChange(e, lessons.setLessonSchedule)} required /><br /> */}
                <input className='styled-date' value={lessons.lessonSchedule} type='date' required /><br />
              </label>
              <hr />
            </div>

            <div>
              <label>
                <h4>판매 상태</h4>
                {/* <select value={lessons.lessonStatus} onChange={(e) => handleInputChange(e, lessons.setLessonStatus)} required> */}
                <select value={lessons.lessonStatus} required>
                  <option value="" disabled>선택</option>
                  <option value='SELL'>판매중</option>
                  <option value='SOLD_OUT'>품절</option>
                </select><br />
                <hr />
              </label>     
            </div>
            <Button type='submit'>수정</Button>
            <Button>삭제</Button>
          </form>
        </div>
      </BasicLayout>
    </div>

  )

}

export default LessonModify;